.banner {
  color: white;
  object-fit: contain;
  height: 448px;
  margin-bottom: 30px;
}

.banner__contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

/* .banner__buttons {
} */

.banner__button {
  cursor: pointer;
  color: rgb(194, 194, 194);
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2rem;
  margin-right: 1rem;
  padding: 0.5rem 2rem 0.5rem 2rem;
  background-color: rgba(51, 51, 51, 0.5);
}

.banner__button:hover {
  color: #000;
  background-color: white;
  transition: all 0.2s;
}

.banner__description {
  width: 45rem;
  line-height: 1.3;
  font-size: 0.8rem;
  /* font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif; */
  font-weight: 100;
  padding-top: 1rem;
  max-width: 360px;
  height: 80px;
}

.banner--fadeBottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}
