.nav {
  position: fixed;
  top: 0;
  width: 100%;
  /* padding: 20px; */
  height: 50px;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  /* background-color: black; */

  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav__black {
  background-color: #111;
}

.nav__logo {
  /* position: fixed; */
  /* left: 20px; */
  width: 100px;
  object-fit: contain;
}

.nav__avatar {
  /* position: fixed; */
  /* right: 20px; */
  width: 30px;
  object-fit: contain;
  margin-right: 15px;
}
